import {
  pathnameToOperatingSystemPath,
  operatingSystemPathToPathname,
  pathnameToRelativePathname,
  isWindowsPath,
  pathnameIsInside,
} from "@jsenv/operating-system-path"
import { hrefToOrigin, hrefToPathname, hrefToScheme } from "@jsenv/href"
import { resolveUrl } from "@jsenv/import-map"
import { fetchUsingHttp } from "./fetchUsingHttp.js"
import { createRequireFromFilename } from "./createRequireFromFilename.js"

const FAKE_SERVER_ORIGIN = `http://example.com`

export const execute = async ({
  compileServerOrigin,
  projectPathname,
  compileIntoRelativePath,
  fileRelativePath,
  collectNamespace,
  collectCoverage,
  executionId,
  // do not log in the console
  // because error handling becomes responsability
  // of node code launching node process
  // it avoids seeing error in platform logs during testing
  errorExposureInConsole = false,
}) => {
  process.once("unhandledRejection", (valueRejected) => {
    throw valueRejected
  })

  // ça ne fixera pas le fait que require se fera ou mauvais endroit
  const executionFilePath = pathnameToOperatingSystemPath(`${projectPathname}${fileRelativePath}`)
  // process.chdir(executionFilePath)
  const executionRequire = createRequireFromFilename(executionFilePath)

  const { SourceMapConsumer } = executionRequire("source-map")
  const { installNodeErrorStackRemapping } = executionRequire("@jsenv/error-stack-sourcemap")

  await fetchUsingHttp(`${compileServerOrigin}/.jsenv/node-platform.js`)
  // eslint-disable-next-line import/no-dynamic-require
  const { nodePlatform } = require(pathnameToOperatingSystemPath(
    `${projectPathname}${compileIntoRelativePath}/.jsenv/node-platform.js`,
  ))
  const { relativePathToCompiledHref, executeFile } = nodePlatform.create({
    compileServerOrigin,
    projectPathname,
  })

  const compiledHref = relativePathToCompiledHref(fileRelativePath)

  const { getErrorOriginalStackString } = installNodeErrorStackRemapping({
    resolveHref: ({ type, specifier, importer }) => {
      if (type === "source" || type === "file-original" || type === "source-map") {
        const importerServerHref = importer
          ? pathToServerHref(importer, { projectPathname, compileServerOrigin })
          : `${compileServerOrigin}${fileRelativePath}`
        const specifierHref = resolveUrl(specifier, importerServerHref)
        const specifierOrigin = hrefToOrigin(specifierHref)
        if (specifierOrigin === compileServerOrigin) {
          const pathname = `${projectPathname}${hrefToPathname(specifierHref)}`
          return `file://${pathname}`
        }
        if (specifierOrigin === FAKE_SERVER_ORIGIN) {
          const pathname = hrefToPathname(specifierHref)
          return `file://${pathname}`
        }
        return specifierHref
      }

      // C:\\Users\\me\\file.js -> file:///C:/Users/me/file.js
      if (isWindowsPath(specifier)) {
        return `file://${operatingSystemPathToPathname(specifier)}`
      }

      // /Users/me/file.js => file:///Users/me/file.js
      if (specifier[0] === "/") {
        return `file://${specifier}`
      }

      // ./foo.js.map -> file:///Users/me/foo.js.map
      if (specifier.slice(0, 2) === "./" || specifier.slice(0, 3) === "../") {
        return resolveUrl(
          specifier,
          importer || `file://${projectPathname}${hrefToPathname(compiledHref)}`,
        )
      }

      // http://, file:/// -> return untouched
      const hasScheme = Boolean(hrefToScheme(specifier))
      if (hasScheme) return specifier

      // internal/process.js for instance
      return `file:///${specifier}`
    },
    SourceMapConsumer,
  })

  return executeFile(compiledHref, {
    collectNamespace,
    collectCoverage,
    executionId,
    errorTransform: async (error) => {
      // code can throw something else than an error
      // in that case return it unchanged
      if (!error || !(error instanceof Error)) return error

      const originalStack = await getErrorOriginalStackString(error)
      error.stack = originalStack
      return error
    },
    errorExposureInConsole,
  })
}

const pathToServerHref = (path, { projectPathname, compileServerOrigin }) => {
  if (isWindowsPath(path)) {
    return pathnameToServerHref(operatingSystemPathToPathname(path), {
      projectPathname,
      compileServerOrigin,
    })
  }

  if (path[0] === "/") {
    return pathnameToServerHref(path, {
      projectPathname,
      compileServerOrigin,
    })
  }

  if (path.startsWith("file:///")) {
    return pathnameToServerHref(operatingSystemPathToPathname(hrefToPathname(path)), {
      projectPathname,
      compileServerOrigin,
    })
  }

  return path
}

const pathnameToServerHref = (pathname, { projectPathname, compileServerOrigin }) => {
  const isInsideProject = pathnameIsInside(pathname, projectPathname)
  if (isInsideProject) {
    return `${compileServerOrigin}${pathnameToRelativePathname(pathname, projectPathname)}`
  }
  return `${FAKE_SERVER_ORIGIN}${pathname}`
}
